<template>
  <el-skeleton v-if="loading" :rows="6" animated />
  <div v-else>
    <TopicItem v-for="topic in topics" :key="topic.id" v-bind="topic" />
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="40"
      :total="pageInfo[tab || 'all'].total"
      :current-page="page"
      @current-change="changePage"
    >
    </el-pagination>
  </div>
</template>

<script>
import { getTopics } from "../plugins/api";
import TopicItem from "../components/TopicItem.vue";
export default {
  data() {
    return {
      topics: [],
      loading: false,
      page:1,
      pageInfo: {
        all: {
          total: 29601,
        },
        good: {
          total: 720,
        },
        share: {
          total: 160,
        },
        ask: {
          total: 80,
        },
        job: {
          total: 2650,
        },
        dev: {
          total: 210,
        },
      },
    };
  },
  props: ["tab"],
  components: { TopicItem },
  watch: {
    // '$route.params.tab':{
    //   handler(){

    //   }
    // }
    tab: {
      async handler(val) {
        this.loading = true;
        const page= this.$route.query.page
        const res = await getTopics({ tab: val || "all", page : page || 1 });
        this.page = page *1 || 1
        this.loading = false;
        this.topics = res.data;
      },
      immediate: true,
    },
  },
  methods: {
    async changePage(page) {
      this.page = page;
      this.loading = true;
      const res = await getTopics({ tab: this.tab || "all", page });
      this.loading = false;
      this.topics = res.data;
      this.$router.push(this.$route.path + `?page=${page}`)
    },
  },
};
</script>

